import React from 'react';
import { Grid, Container, Segment, Header, Image, Tab, Input, Button, Icon } from 'semantic-ui-react'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.myCanvas = React.createRef();
    this.handleChange = this.handleChange.bind(this);
    this.renderDDA = this.renderDDA.bind(this);
    this.renderMidPoint = this.renderMidPoint.bind(this);
  } 

  state = {
    x1: 0,
    y1: 0,
    x2: 0,
    y2: 0,
    x1_b: 0,
    y1_b: 0,
    x2_b: 0,
    y2_b: 0,
    x_c: 0,
    y_c: 0,
    r_c: 0
  }

  handleChange = (event) => {
    this.setState({ [event.currentTarget.name]: event.currentTarget.value })
  }

  renderDDA = () => {
    let c = this.myCanvas.current;
    let ctx=c.getContext("2d");
    ctx.fillStyle = "rgba("+120+","+150+","+255+","+(255/255)+")";
    const x1 = parseInt(this.state.x1), y1 = parseInt(this.state.y1);
    const x2 = parseInt(this.state.x2), y2 = parseInt(this.state.y2);
    const slope = (y2-y1) / (x2-x1);
    if(x1 === undefined || y1 === undefined || x2 === undefined || y2 === undefined ) return;
    
    let x = 0, y = 0;
    x = parseInt(x1);
    y = parseInt(y1);
    ctx.font = "9px Arial";
    ctx.fillText('('+x1+','+y1+')',x1,299-y1);
    ctx.fillText('('+x2+','+y2+')',x2,299-y2);
    let i = 0;
    ctx.fillRect(x,299-y,1,1);
    while(true) {
      if(x === x2 || y === y2) break; 
      if(slope < 1) {
        x = x + 1;
        y = y + slope;
      } else if(slope > 1) {
        x = x + 1/slope;
        y = y + 1;
      } else {
        x = x + 1;
        y = y + 1;
      }
      ctx.fillRect(Math.round(x),Math.round(299-y),1,1);
    }
  }

  renderBresehams = () => {
    let c = this.myCanvas.current;
    let ctx=c.getContext("2d");
    ctx.fillStyle = "rgba("+220+","+110+","+80+","+(255/255)+")";
    const x1 = parseInt(this.state.x1_b), y1 = parseInt(this.state.y1_b);
    const x2 = parseInt(this.state.x2_b), y2 = parseInt(this.state.y2_b);
    if(x1 === undefined || y1 === undefined || x2 === undefined || y2 === undefined ) return;
    const dy = (y2-y1);
    const dx = (x2-x1);
    const dy_2 = 2*(y2-y1);
    const dy_2_dx = (2*(y2-y1) - 2*(x2-x1));
    let pk = ((2*dy) - dx);
    let i = 0, x = x1, y = y1;
    ctx.font = "9px Arial";
    ctx.fillText('('+x1+','+y1+')',x1,299-y1);
    ctx.fillText('('+x2+','+y2+')',x2,299-y2);
    ctx.fillRect(x,299-y,1,1);
    for(i=0;i<dx;i++) {
      if(pk < 0) {
        x = x + 1;
        pk = pk + dy_2;
      } else {
        x = x + 1;
        y = y + 1;
        pk = pk + dy_2_dx;
      }
      ctx.fillRect(x,299-y,1,1);
    }
  }

  renderMidPoint = () => {
    const c = this.myCanvas.current;
    const ctx=c.getContext("2d");
    const r = parseInt(this.state.r_c);
    let x = parseInt(this.state.r_c), y = 0;
    const x_centre = parseInt(this.state.x_c), y_centre = parseInt(this.state.y_c);
    
    console.log(x_centre, y_centre, r);

    ctx.fillStyle = "rgba("+120+","+230+","+120+","+(255/255)+")";
    ctx.fillText('('+x_centre+','+y_centre+')',x_centre,299-y_centre);

    ctx.fillRect(x + x_centre, 299-(y + y_centre), 1, 1);
    console.log(x + x_centre, 299-(y + y_centre));
    let P = 1 - r;
    
    while (x > y)
    { 
        y++;
        // Mid-point is inside or on the perimeter
        if (P <= 0)
            P = P + 2*y + 1;
             
        // Mid-point is outside the perimeter
        else
        {
            x--;
            P = P + 2*y - 2*x + 1;
        }
         
        // All the perimeter points have already been printed
        if (x < y)
            break;
         
        // Printing the generated point and its reflection
        // in the other octants after translation
        ctx.fillRect(x + x_centre, 299-(y + y_centre), 1, 1);
        ctx.fillRect(-x + x_centre, 299-(y + y_centre), 1, 1);
        ctx.fillRect(x + x_centre, 299-(-y + y_centre), 1, 1);
        ctx.fillRect(-x + x_centre, 299-(-y + y_centre), 1, 1);
         
        // If the generated point is on the line x = y then 
        // the perimeter points have already been printed
        if (x != y)
        {
          ctx.fillRect(y + x_centre, 299-(x + y_centre), 1, 1);
          ctx.fillRect(-y + x_centre, 299-(x + y_centre), 1, 1);
          ctx.fillRect(y + x_centre, 299-(-x + y_centre), 1, 1);
          ctx.fillRect(-y + x_centre, 299-(-x + y_centre), 1, 1);
        }
    }

  }

  panes = [
    { menuItem: 'DDA', render: () => 
      <Tab.Pane>
        <Input value={this.state.x1} name="x1" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='x1' placeholder='x1' />
        <Input value={this.state.y1} name="y1" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='y1' placeholder='y1' />
        <Input value={this.state.x2} name="x2" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='x2' placeholder='x2' />
        <Input value={this.state.y2} name="y2" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='y2' placeholder='y2' />
        <div style={{ display: 'flex', justifyContent: 'center', marginTop: 15 }}>
          <Button onClick={this.renderDDA} icon labelPosition='right'>
            Render
            <Icon name='heart' />
          </Button>
  
        </div>
        
        
      </Tab.Pane> },
    { menuItem: 'Bresenhams', render: () => 
      <Tab.Pane>
        <Input value={this.state.x1_b} name="x1_b" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='x1' placeholder='x1' />
        <Input value={this.state.y1_b} name="y1_b" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='y1' placeholder='y1' />
        <Input value={this.state.x2_b} name="x2_b" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='x2' placeholder='x2' />
        <Input value={this.state.y2_b} name="y2_b" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='y2' placeholder='y2' />
        <div style={{ display: 'flex', justifyContent: 'center', marginTop: 15 }}>
          <Button onClick={this.renderBresehams} icon labelPosition='right'>
            Render
            <Icon name='heart' />
          </Button>
        </div>
        
      </Tab.Pane> },
    { menuItem: 'Mid Point', render: () => 
      <Tab.Pane>
        <Input value={this.state.x_c} name="x_c" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='x' placeholder='x' />
        <Input value={this.state.y_c} name="y_c" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='y' placeholder='y' />
        <Input value={this.state.r_c} name="r_c" onChange={this.handleChange} type='number' style={{ margin: 2 }} label='r' placeholder='r' />
        <div style={{ display: 'flex', justifyContent: 'center', marginTop: 15 }}>
          <Button onClick={this.renderMidPoint} icon labelPosition='right'>
            Render
            <Icon name='heart' />
          </Button>
        </div>
      </Tab.Pane> },
  ]



  render() {
    return(
      <div>
        <Header as='h1' style={{ backgroundColor: '#ea4c4c', color: '#fff9e0', padding: 10 }}>
          <Image circular style={{ backgroundColor: '#fff' }} src='myAvatar.png' /> Meetesh's Shack
        </Header>
        <Container>
          <Grid stackable columns={2}>
            <Grid.Column>
              <Segment style={{ backgroundColor: '#596e79' }}>
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                  <canvas style={{ borderRadius: 2, backgroundColor: '#fff' }} ref={this.myCanvas} width="300" height="300">
                  </canvas>
                </div>
                <div style={{ marginTop: 10, display: 'flex', justifyContent: 'center' }}>
                  <Button onClick={() => {
                    this.myCanvas.current.getContext("2d").clearRect(0, 0, this.myCanvas.current.width, this.myCanvas.current.height);
                  }} content='Clear Canvas' />
                </div>
              </Segment>
            </Grid.Column>
            <Grid.Column>
              <Segment>
                <Tab menu={{ secondary: true }} panes={this.panes} />
              </Segment>
            </Grid.Column>
          </Grid>
        </Container>
      </div>
    );
  }
}


export default App;


